@use '../base/variables' as v;
@use '../base/mixins' as m;

.paginacion {
    margin-top: 5rem;
    margin-bottom: 3rem;
    display: flex;
    gap: 1rem;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;

    @include m.tablet {
        justify-content: flex-end;
    }

    &__enlace {
        color: v.$azul;
        font-size: 1.6rem;

        &--texto {
            padding: 1rem;
            border: 1px solid v.$azul;
            border-radius: .5rem;

            &:hover {
                background-color: v.$azul;
                color: v.$blanco;
            }
        }
        &--numero {
            font-weight: v.$bold;
            //oculta los números en mobile
            display: none;

            @include m.tablet{
                //muestra los números en tablet y más
                display: inline;
            }
        }
        &--actual {
            color: v.$gris;
            font-weight: v.$bold;
            //oculta los números en mobile
            display: none;

            @include m.tablet{
            //muestra los números en tablet y más
            display: inline;
            }
        }
    }
}
